.footer {
  background-color: var(--dark-color);
  padding: 40px 0;

  &__logo {
    display: inline-block;
    margin-bottom: 5px;

    @include mobile {
      margin-bottom: 20px;
    }
  }

  &__container {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;

    @include mobile {
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
  }

	&__copy {
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    color: rgba(255, 255, 255, 0.7);

    @include mobile {
      display: none;
    }

    &--mobile {
      display: none;

      @include mobile {
        font-size: 14px;
        display: inline-block;
      }
    }

	}

  &__phone {
    @include mobile {
      margin-bottom: 40px;
    }
  }


}
