.graph-modal {
  &::before {
    @include tablet {
      display: none;
    }
  }

  &__close {
    right: 40px;
    top: 40px;
    width: auto;
    height: auto;
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    color: var(--text-color);

    @include mobile {
      top: 10px;
      right: 10px;
      width: 23px;
      height: 23px;
      font-size: 0;
      background-color: var(--dark-color);
      mask-image: url(../img/close.svg);
      mask-position: center;
      mask-size: 20px 20px;
      mask-repeat: no-repeat;
    }
  }
}

.graph-modal {
  @include mobile {
    padding: 0 10px;
  }
}

.popup {
  padding: 40px;
  width: 100%;
  max-width: 940px;

  &__top {
    display: flex;
    margin-bottom: 40px;

    @include mobile {
      margin-bottom: 20px;
    }
  }

  &__image {
    margin-right: 40px;

    @include mobile {
      margin-right: 10px;
    }
  }

  &__name {
    margin-bottom: 10px;
    font-weight: 600;
    font-size: 24px;
    line-height: 160%;
    color: var(--dark-color);

    @include mobile {
      font-size: 16px;
    }
  }

  &__post {
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    color: var(--text-color);

    @include mobile {
      min-width: 180px;
      font-size: 12px;
      line-height: 140%;
    }
  }

  &__bottom-content {
    padding-top: 20px;
    max-height: 375px;

    @include mobile {
      max-height: 293px;
    }
  }

  &__descr {
    margin-bottom: 40px;

    @include mobile {
      margin-bottom: 20px;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 5px;
    font-weight: 400;
    font-size: 20px;
    line-height: 160%;
    color: var(--dark-color);

    @include mobile {
      font-size: 14px;
      margin-bottom: 2.5px;
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 600;
    font-size: 24px;
    line-height: 160%;
    color: var(--dark-color);

    @include mobile {
      font-size: 16px;
      margin-bottom: 10px;
    }
  }

  .simplebar-track.simplebar-vertical {
    border-radius: 40px;
    width: 5px;
    background: rgba(20, 16, 36, 0.08);
    margin: 20px 0;
  }

  .simplebar-scrollbar:before {
    left: 0;
    right: 0;
    border-radius: 40px;
    width: 5px;
    background-color: var(--dark-color);
    opacity: 1;
  }

  @include smallest-tablet {
    padding-top: 80px;
  }

  @include mobile {
    padding: 20px;
    padding-top: 30px;
  }
}

.tabs {

  &__menu {
    border-bottom: 1px solid rgba(20, 16, 36, 0.11);
    padding-bottom: 20px;

    @include mobile {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }

  &__selected {
    width: 100%;
    max-height: 46px;
    display: none;
    align-items: center;
    justify-content: center;
    background: var(--accent-color);
    cursor: pointer;

    &-text {
      margin-right: 5px;
      font-size: 16px;
      line-height: 160%;
      text-align: center;
      color: var(--light-color);
    }

    @include mobile {
      display: flex;
    }
  }

  &__nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 75%;

    &-item {
      @include mobile {
        padding: 10px 0;
      }
    }

    @include mobile {
      width: 100%;
      max-width: 100%;
      flex-direction: column;
      align-items: center;
      background-color: var(--light-color);
      color: var(--dark-color);
      opacity: 0;
      max-height: 0;
      transition: all 0.5s;
    }

    &-active {
      opacity: 1;
      max-height: 240px;
    }
  }

  &__nav-btn {
    font-weight: 400;
    font-size: 20px;
    line-height: 160%;
    color: var(--dark-color);

    &:focus {
      color: var(--accent-color);
      background-color: transparent;
    }

    &--active {
      outline: none;
      color: var(--accent-color);
      background-color: transparent;
    }

    @include mobile {
      font-size: 16px;
    }
  }


}
