.section-title {
  font-weight: 600;
  font-size: 50px;
  line-height: 160%;
  color: var(--dark-color);
  margin-bottom: 60px;

  @include smallest-tablet {
    line-height: 60px;
  }

  @include mobile {
    text-align: center;
    font-size: 30px;
    line-height: 140%;
    margin-bottom: 40px;
  }
}
