.benefits {
  background-color: #f9f9f9;

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 40px;

    @include mobile {
      grid-template-columns: repeat(8, 1fr);
      grid-template: 30px;
    }
  }

  &__item {
    grid-column: 4 span;

    @include high-desktop {
      grid-column: 6 span;
    }

    @include mobile {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      grid-column: 8 span;
    }
  }

  &__subtitle {
    display: inline-flex;
    align-items: center;
    font-weight: 600;
    font-size: 24px;
    line-height: 160%;
    color: var(--dark-color);
    margin-bottom: 20px;

    @include mobile {
      margin-bottom: 10px;
      font-size: 20px;
    }
  }

  &__descr {
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    color: var(--text-color);

    @include mobile {
      font-size: 16px;
      text-align: center;
    }
  }
}
