.about {
  background-color: var(--light-color);

  &__descr {
    max-width: 892px;
    margin: 0;
    margin-bottom: 60px;
    font-weight: 400;
    font-size: 30px;
    line-height: 160%;
    color: var(--dark-color);

    &-highlight {
      font-weight: 600;
    }

    @include mobile {
      font-size: 20px;
      margin-bottom: 40px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 40px;

    @include mobile {
      grid-template-columns: repeat(8, 1fr);
    }
  }

  &__item {
    grid-column: 3 span;

    @include high-desktop {
      grid-column: 6 span;
    }

    @include mobile {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      gap: 30px;
      grid-column: 8 span;
    }
  }

  &__digit {
    font-weight: 600;
    font-size: 70px;
    line-height: 160%;
    color: var(--accent-color);

    @include mobile {
      font-size: 60px;
      line-height: 87px;
    }
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    color: var(--text-color);

    @include mobile {
      text-align: center;
      font-size: 16px;
    }
  }

}
