.hero {
  padding-top: 160px;
  min-height: 800px;
  background-color: var(--dark-color);

  &__container {
    @include flex-v-center;
  }

  &__content {
    max-width: 516px;

    @include high-tablet {
      max-width: none;
    }

    @include mobile {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }

  &__image {
    transform: translateX(167px);

    @include high-tablet {
      display: none;
    }
  }

  &__title {
    margin: 0;
    font-weight: 600;
    font-size: 70px;
    line-height: 160%;
    text-transform: uppercase;
    color: var(--light-color);
    margin-bottom: 50px;

    @include mobile {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      font-size: 30px;
      text-align: center;
    }
  }

  &__subtitle {
    max-width: 90%;
    display: block;
    font-weight: 400;
    font-size: 20px;
    line-height: 160%;
    text-transform: initial;

    @include tablet {
      max-width: 100%;
    }

    @include mobile {
      font-size: 16px;
      margin-top: 10px;
    }
  }

  @include high-tablet {
    padding-bottom: 100px;
    min-height: auto;
  }

  @include mobile {
    padding-top: 100px;
    padding-bottom: 40px;
  }
}
